import React, {useRef, useEffect, useCallback, forwardRef, useState} from 'react';
import './ContinuousPatterns.css'; // 




const ContinuousPatterns = forwardRef(({ sketchDataURL, selectedColor, connectionType, currentStep, onSave,spacing,setSpacing,imgscale,setImgscale }, ref) => {
    const canvasRef = useRef(null);
    const [offsetX, setOffsetX] = useState(0); // X

    //  canvas 
    const setCanvasSize = useCallback(() => {
        const canvas = canvasRef.current;
        if (canvas && canvas.parentElement) {

            canvas.width = 4096;
            canvas.height = 2048;

        }
    }, []);

    //  canvas
    const drawPattern = useCallback(() => {
        const canvas = canvasRef.current;
        const context = canvas.getContext('2d');

        //  canvas
        clearCanvas();
        //  canvas 


        context.fillStyle = selectedColor; //  selectedColor 
        context.fillRect(0, 0, canvas.width, canvas.height); //  canvas

        const img = new Image();

        img.onload = () => {
            // const scaledWidth = img.width * 0.25+spacing;
            // const scaledHeight = img.height * 0.25+spacing;
            const scaledWidth = img.width*imgscale +spacing;
            const scaledHeight = img.height*imgscale +spacing;
            const numTilesY = Math.ceil(canvas.height / scaledHeight);
            const offsetMap = {
                '': 0,
                '_1/4': 0.25,
                '_1/3': 1 / 3,
                '_1/2': 0.5,
                '_2/3': 2 / 3,
                '_3/4': 0.75,
            };
            const jumpOffset = offsetMap[connectionType] || 0;
     console.log(connectionType)
            let currentOffsetX = 0;

            for (let y = 0; y < numTilesY; y++) {
                currentOffsetX = y * scaledWidth * jumpOffset % scaledWidth;

                if (currentOffsetX > 0) {
                    context.drawImage(img, currentOffsetX - scaledWidth, y * scaledHeight, scaledWidth, scaledHeight);
                }

                for (let x = currentOffsetX - scaledWidth; x < canvas.width; x += scaledWidth) {
                    context.drawImage(img, x, y * scaledHeight, scaledWidth, scaledHeight);
                }
            }

            //  offsetX  X 
            setOffsetX(currentOffsetX);
            handleSave();
        };

        img.src = sketchDataURL;
        console.log(1)
        console.log( context.fillStyle)
        console.log(2)
        console.log(selectedColor)

    }, [sketchDataURL, connectionType, currentStep, selectedColor, setOffsetX,spacing,imgscale]); //  setOffsetX 
    const handleSave = useCallback(() => {
        const canvas = canvasRef.current;
        if (canvas) {
            const imageData = canvas.toDataURL("image/png", 1, 300);
            onSave(imageData); // 
        }
    }, [onSave]);
    
    // 
    const debounce = useCallback((func, wait) => {
        let timeout;
        return (...args) => {
            clearTimeout(timeout);
            timeout = setTimeout(() => {
                func.apply(this, args);
            }, wait);
        };
    }, []);
    const clearCanvas = () => {
        const canvas = canvasRef.current;
        if (!canvas) return;
        const context = canvas.getContext('2d');
        // 
        context.clearRect(0, 0, canvas.width, canvas.height);
    };



    useEffect(() => {
        clearCanvas(); // 

        //  canvas 
        setCanvasSize();

        // 
        drawPattern();

        // 
        const handleResize = debounce(() => {
            clearCanvas(); // 
            setCanvasSize();
            drawPattern(); // 
        }, 250);

        // 
        window.addEventListener('resize', handleResize);

        // 
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, [debounce, setCanvasSize, drawPattern, sketchDataURL, selectedColor, connectionType, currentStep, spacing,imgscale]); // spacing





    return (
         // <div className="continuous-patterns" style={{ backgroundColor: selectedColor }}>
             <div className="continuous-patterns" >
        <canvas ref={canvasRef} id="myCanvas" style={{ width: '60vw', height: '60vh' }}/>
        {/*<button onClick={handleSave}>Save next</button>*/}
        {/*<button onClick={saveCanvasAsImage}>Save as Image</button>*/}
    </div>
    );
});

export default ContinuousPatterns;