import React from 'react';
import './MaterialLibrary.css'; // CSS

function MaterialLibrary({ materials }) { // propsmaterials
  // imagespropsmaterials
 // console.log(materials)
 //  MaterialLibrary 
// MaterialLibrary 
function handleDragStart(event, imageUrl) {
  //  URL 
  const dragData = JSON.stringify({ type: 'library', url: imageUrl });
  event.dataTransfer.setData('application/json', dragData);
}


  return (
    <div>
      <div className="title-bar">
        <span> </span>
      </div>
      <div className="image-container">
        {materials && materials.map((image, index) => ( // materials
         <img
         key={index}
         src={image}
         draggable="true"
         onDragStart={(e) => handleDragStart(e, image)}
         alt={`Material ${index}`}
         className="material-image"
       />
       
        ))}
      </div>
    </div>
  );
}

export default MaterialLibrary;
